}

.photographycards {
  width: 500px;
  height: 500px;
  justify-content: center;
  margin-left: -30px;
}
.dealer-service-photography {
  font-family: cursive;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
}
.service-name-photography {
  font-family: cursive;

  text-align: center;
}
.photography-img {
  margin-left: 160px;
